import React from 'react';
import { Link, NavLink } from 'react-router-dom';
import './Navbar.css';
import logo from './ECSCircleLogo.jpg';

class Navbar extends React.Component {

    displayMenu() {
        let navbar = document.getElementById('Navbar');

        if(navbar.className === "Navbar") {
            navbar.className += " responsive";
        } else {
            navbar.className = "Navbar";
        }
    }

    render() {
        return (
            <div className="Navbar" id="Navbar">
                Hello
                <Link to="/emeraldcitysmoothie/">
                    <img className="logo" src={logo} alt="Emerald City Smoothie Logo"></img>
                </Link>
                <div className="contents" id="contents">
                    <NavLink to="/emeraldcitysmoothie/" exact className="button">Home</NavLink>
                    <NavLink to="/emeraldcitysmoothie/menu" className="button">Menu</NavLink>
                    <NavLink to="/emeraldcitysmoothie/contact" className="button">Contact</NavLink>
                    <NavLink to="/emeraldcitysmoothie/about" className="button">About</NavLink>
                    <NavLink to="/emeraldcitysmoothie/order" className="button order">Order Now</NavLink>
                    <a href="javascript:void(0);" class="icon" onclick={this.displayMenu}>
                        <i class="fa fa-bars"></i>
                    </a>
                </div>
            </div>
        );
    }
}

export default Navbar;